.app{
    background: var(--primary-color);
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title{
    color: var(--secondary-color);
    font-weight: 300;
    font-size: 250%;
}

.actionButton{
    background: transparent;
    outline: 0 none;
    padding: 1% 3%;
    color: var(--secondary-color);
    font-size: 80%;
    text-transform: uppercase;
    font-weight: 300;
    border: 1px solid var(--secondary-color);
    cursor: pointer;
    transition: 0.3s;
}

.actionButton:hover{
    background: var(--secondary-color);
    color: var(--primary-color);
}

.footer{
    background: var(--primary-color);
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 3% 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--secondary-color);
}

.link{
    color: var(--secondary-color);
    text-decoration: none;
}
